import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class Timeline extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		let {
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('timeline',className);

		return (
			<ul
				{...others}
				className={klassName}
			>
				{children}
			</ul>
		);
	}
};

Timeline.propTypes = {
	children: PropTypes.node,
	className: PropTypes.string,
};

Timeline.defaultProps = {
};

export default Timeline;